<template>
  <v-container fluid>
    <v-row>
      <!-- Liste des séances -->
      <v-col cols="12" md="4">
        <v-card outlined>
          <v-card-title>Mes séances</v-card-title>
          <v-divider />
          <v-list two-line>
            <v-list-item
                v-for="session in sessions"
                :key="session.id"
                @click="selectSession(session)"
                :class="{ 'grey lighten-4': selectedSession?.id === session.id }"
            >
              <v-list-item-content>
                <v-list-item-title>{{ session.subject }}</v-list-item-title>
                <v-list-item-subtitle>
                  {{ formatDate(session.startTime) }} • Groupe: {{ session.group.name }}
                </v-list-item-subtitle>
              </v-list-item-content>
            </v-list-item>
            <v-list-item v-if="!sessions.length">
              <v-list-item-content>
                <v-progress-circular v-if="loadingSessions" indeterminate color="primary" />
                <span v-else>Aucune séance trouvée</span>
              </v-list-item-content>
            </v-list-item>
          </v-list>
        </v-card>
      </v-col>

      <!-- Détails de la séance -->
      <v-col cols="12" md="8">
        <div v-if="!selectedSession">
          <v-alert type="info" outlined>Sélectionnez une séance</v-alert>
        </div>
        <div v-else>
          <!-- Infos et génération QR -->
          <v-card class="mb-4" outlined>
            <v-card-title>Détails de la séance</v-card-title>
            <v-card-text>
              <session-info :session="selectedSession" />
              <div class="mt-2 text-caption">
                Enseignant: {{ selectedSession.teacher.firstname }}
                {{ selectedSession.teacher.lastname }}
              </div>
            </v-card-text>
          </v-card>

          <!-- QR Code -->
          <v-card class="mb-4" outlined>
            <v-card-title>QR Code de présence</v-card-title>
            <v-card-text>
              <qr-code-generator
                  :sessionId="selectedSession.id"
                  @qr-generated="handleQrGenerated"
              />
            </v-card-text>
          </v-card>

          <!-- Bouton PDF -->
          <v-btn color="primary" @click="downloadPdf" :loading="pdfLoading" class="mb-4">
            <v-icon left>mdi-file-pdf-box</v-icon>
            Télécharger fiche
          </v-btn>

          <!-- Liste des présences -->
          <attendance-list
              :attendances="attendances"
              :loading="loadingAttendances"
              @refresh="refreshAttendances"
          />
        </div>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import AttendanceList from '@/components/teacher/AttendanceList'
import QrCodeGenerator from '@/components/teacher/QrCodeGenerator'
import SessionInfo from '@/components/teacher/SessionInfo'
import api from '@/services/api'
import { format } from 'date-fns'

export default {
  name: 'SessionView',
  components: { SessionInfo, QrCodeGenerator, AttendanceList },
  data() {
    return {
      sessions: [],
      selectedSession: null,
      attendances: [],
      pdfLoading: false,
      loadingSessions: false,
      loadingAttendances: false,
      qrData: null
    }
  },
  async created() {
    await this.loadSessions()
  },
  methods: {
    formatDate(date) {
      return format(new Date(date), 'dd/MM/yyyy HH:mm')
    },

    async loadSessions() {
      this.loadingSessions = true
      try {
        const token = localStorage.getItem('token')
        if (!token) throw new Error('Authentification requise')

        const response = await api.get('/api/sessions/teacher', {
          headers: { Authorization: `Bearer ${token}` }
        })

        this.sessions = response.data || []
      } catch (error) {
        console.error('Erreur chargement sessions:', {
          message: error.message,
          response: error.response?.data
        })
        this.showError(error.response?.data?.message || 'Erreur lors du chargement des sessions')

        if (error.response?.status === 401) {
          this.$router.push('/login')
        }
      } finally {
        this.loadingSessions = false
      }
    },

    async selectSession(session) {
      this.selectedSession = session
      await this.refreshAttendances()
    },

    async refreshAttendances() {
      if (!this.selectedSession) return

      this.loadingAttendances = true
      try {
        const token = localStorage.getItem('token')
        if (!token) throw new Error('Authentification requise')

        const response = await api.get(
            `/api/attendances/session/${this.selectedSession.id}`,
            { headers: { Authorization: `Bearer ${token}` } }
        )

        this.attendances = response.data || []
      } catch (error) {
        console.error('Erreur chargement présences:', {
          message: error.message,
          status: error.response?.status
        })

        this.showError(error.response?.data?.message || 'Erreur lors du chargement des présences')

        if (error.response?.status === 401) {
          this.$router.push('/login')
        }
      } finally {
        this.loadingAttendances = false
      }
    },

    async downloadPdf(dailyMode = false) {
      if (!this.selectedSession) return;

      this.pdfLoading = true;
      try {
        const token = localStorage.getItem('token');
        if (!token) throw new Error('Authentification requise');

        let endpoint, filename;

        if (dailyMode) {
          const date = new Date(this.selectedSession.startTime).toISOString().split('T')[0];
          endpoint = `/api/pdf/generate-daily/${date}/${this.selectedSession.groupId}`;
          filename = `emargement_groupe_${this.selectedSession.groupId}_${date}.pdf`;
        } else {
          endpoint = `/api/pdf/generate/${this.selectedSession.id}`;
          filename = `emargement_${this.selectedSession.subject}_${this.selectedSession.id}.pdf`;
        }

        const response = await api.get(endpoint, {
          responseType: 'blob',
          headers: {
            Authorization: `Bearer ${token}`,
            'Cache-Control': 'no-cache'
          }
        });

        const url = window.URL.createObjectURL(new Blob([response.data]));
        const link = document.createElement('a');
        link.href = url;
        link.setAttribute('download', filename);
        document.body.appendChild(link);
        link.click();

        setTimeout(() => {
          window.URL.revokeObjectURL(url);
          document.body.removeChild(link);
        }, 100);

        this.showSuccess('Fiche d\'émargement générée avec succès');
      } catch (error) {
        console.error('Erreur téléchargement PDF:', {
          message: error.message,
          status: error.response?.status
        });

        this.showError(error.response?.data?.message || 'Erreur lors de la génération du PDF');

        if (error.response?.status === 401) {
          this.$router.push('/login');
        }
      } finally {
        this.pdfLoading = false;
      }
  },

    handleQrGenerated(data) {
      this.qrData = data
      this.showSuccess('QR Code généré avec succès')
      this.refreshAttendances() // Rafraîchir les présences après génération
    },

    handleQrError(error) {
      console.error('Erreur du composant QR:', error)
      this.showError(error.message || 'Erreur avec le QR Code')
    },

    showError(message) {
      try {
        if (this.$toast?.error) {
          this.$toast.error(message, {
            position: 'top-right',
            timeout: 3000
          })
        } else {
          console.error('Erreur:', message)
        }
      } catch (toastError) {
        console.error('Erreur dans showError:', toastError)
      }
    },

    showSuccess(message) {
      try {
        if (this.$toast?.success) {
          this.$toast.success(message, {
            position: 'top-right',
            timeout: 2000
          })
        }
      } catch (toastError) {
        console.error('Erreur dans showSuccess:', toastError)
      }
    }
  }
}
</script>

<style scoped>
.v-progress-circular {
  margin-right: 12px;
}
</style>