<template>
  <v-card outlined>
    <v-card-title>Présences</v-card-title>
    <v-card-text>
      <v-simple-table>
        <thead>
        <tr>
          <th>N°</th>
          <th>Étudiant</th>
          <th>Email</th>
          <th>Validé par</th>
          <th>Manuel</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(att, idx) in attendances" :key="att.id">
          <td>{{ idx + 1 }}</td>
          <td>{{ att.student.lastname }} {{ att.student.firstname }}</td>
          <td>{{ att.student.email }}</td>
          <td>{{ att.validator?.lastname || '-' }}</td>
          <td>
            <v-btn icon small @click="markPresent(att)">
              <v-icon color="green">mdi-check</v-icon>
            </v-btn>
          </td>
        </tr>
        </tbody>
      </v-simple-table>
      <v-btn text small @click="$emit('refresh')">Rafraîchir</v-btn>
    </v-card-text>
  </v-card>
</template>

<script>
import api from '@/services/api'

export default {
  props: {
    attendances: Array,
    loading: Boolean
  },
  methods: {
    async markPresent(att) {
      try {
        await api.put(`/api/attendances/${att.id}`, {status: 'present'})
        this.$emit('refresh')
      } catch (e) {
        console.error(e)
      }
    }
  }
}
</script>
